.About{
    clear: both;
    width: 100%;
    display: flex;
    flex-direction: column;
    .about-content{
        width: calc(100% - 28rem);
        padding: 5rem 14rem;
        margin: 0 auto;
        h1{
            font-family: GowunBatang-Regular;
            font-size: 3rem;
            padding: 0 0 20px 0;
        }
        p{
            font-size: 2rem;
            padding: 20px 0;
            color: #777
        }
    }

    .introduce{
        .introduce-area{
            display: flex;
            flex-direction: row;
            .introduce-left, .introduce-right{ width: 50% }
            .introduce-right{
                h2{
                    font-family: NotoSerifKR-Regular;
                    font-size: 2.8rem;
                    line-height: 5rem;
                }
                p{
                    font-size: 1.8rem;
                    line-height: 5rem;
                }
            }
        }
    }

    .career{
        .career-line{
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            .career-item{
                width: calc(20% - 10rem);
                margin: 0 5rem;
                border-radius: 15px;
                color: #fff;
                .title{
                    font-size: 2rem;
                    text-align: center;
                    padding: 1.5rem;
                }
                .year{
                    font-size: 2rem;                    
                    text-align: center;
                    padding: 1.5rem;
                }
            }
            .career-item:nth-child(1){
                background-color: #D1FFCA;
                color: #000;
            }
            .career-item:nth-child(2){
                background-color: #00A2C9;
            }
            .career-item:nth-child(3){
                background-color: #5B5AFF;
            }
            .career-item:nth-child(4){
                background-color: #A566FF;
            }
        }
    }

    .activity{
        display: flex;
        flex-direction: column;
        .activity-area{
            margin: 0 0 6rem 0;
            .activity-title{
                margin: 3rem 0;
                font-size: 3rem;
                text-align: center;
                font-family: NotoSerifKR-Regular;
                font-weight: 700;
            }
            .activity-content{
                p{
                    a{font-size: 1.6rem;color: #aaa;transition: .5s all ease-in-out;}
                    a:hover{color: #000}
                }
                h3{font-size: 2rem; font-weight: 700; padding: 4rem 0 0 0;}
                .favorite-list{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    div{
                        cursor: pointer;
                        text-align: center;
                        width: 25%;
                        transition: .5s all ease-in-out;
                        .img{
                            width: calc(100% - 4rem);
                            padding: 2rem;
                            img{
                                width: 100%;
                            }
                        }
                        span{
                            font-size: 1.4rem;
                            color: #000;
                            transition: .5s all ease-in-out;
                        }
                    }
                    div:hover{
                        opacity: 0.8;
                        span{
                            color: #3764e0
                        }
                    }
                }
            }
        }
    }
}


@media screen and (max-width: 470px) {
    .About{
        .about-content{
            width: 100%;
            padding: 1rem;
        }
    }
}